@media screen and (min-width: 791px) {
  .main__text_visible {
    display: none;
  }
}

@media screen and (max-width: 790px) {
  .main__text_visible {
    display: block;
    padding: 0px 0 11px 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: #272f40;
  }
}
